<template>
  <div>
    <div v-if="isBerekeningDone === true && teBerekenenIsLening === true" >
      <div class="bs-uitkomst__resultaat" v-if="berekendTotaalBedrag !== '0,00'">
        <p>Je kan op <span class="bs-uitkomst__resultaat-color">{{jaarFull}} maand(en)</span> een lening aangaan van</p>
        <div class="bs-uitkomst__resultaat__bedrag__wrap">
          <p class="bs-uitkomst__resultaat__bedrag">{{ berekendTotaalBedrag }}</p>
          <p class="bs-uitkomst__resultaat__eur">EUR</p>
        </div>
      </div>
      <form action="https://www.hypotheek.be/nl/uw-lening-aanvragen" method="get">
      <!-- <form action="https://auxifina.be/nl/uw-lening-aanvragen" method="get"> -->
        <input type="text" v-bind:value="teBerekenenIsLening === true ? berekendTotaalBedragCijfers : totaalBedrag" name="amount" class="hidden">
        <input type="text" v-bind:value="teBerekenenIsLening === true ? jaarFull : jaarMonth" name="duration" class="hidden">
        <input type="text" v-bind:value="teBerekenenIsLening === true ? jkpFull : jkpMonthCijfer" name="taeg" class="hidden" >
        <input type="text" v-bind:value="teBerekenenIsLening === true ? maandelijksBedrag : berekendBedragPerMaand" name="monthlyrepayment" class="hidden" >
        <input type="text" v-bind:value="teBerekenenIsLening === true ? totaalbedragInterest : totaalbedragInterestMaand" name="repayment" class="hidden" >
        <input type="text" value="lening-op-afbetaling" name="krediet-type" class="hidden" >
        <button class="bs-uitkomst__aanvragen" type="submit">Lening aanvragen</button>
      </form>
      <form class="form__extra" action="https://www.hypotheek.be/nl/contactformulier-consument/een-afspraak-maken" method="get">
      <!-- <form class="form__extra" action="https://auxifina.be/nl/contact" method="get"> -->
        <input type="text" v-bind:value="berekendTotaalBedrag" name="bedrag" class="hidden">
        <input type="text" v-bind:value="jaarFull" name="periode" class="hidden">
        <input type="text" v-bind:value="true" name="simulator" class="hidden">
        <input type="text" value="lening-op-afbetaling" name="krediet-type" class="hidden" >
        <button class="bs-uitkomst__aanvragen submit__formulier" type="submit">Of vraag een gepersonaliseerde simulatie</button>
      </form>
    </div>
    <div v-else-if="isBerekeningDone">
      <div class="bs-uitkomst__resultaat" v-if="berekendBedragPerMaand !== '0,00'">
        <p>Maandelijks te betalen bedrag over een periode van <span class="bs-uitkomst__resultaat-color">{{jaarMonth}} maand(en)</span></p>
        <div class="bs-uitkomst__resultaat__bedrag__wrap">
          <p class="bs-uitkomst__resultaat__bedrag">{{ berekendBedragPerMaandLocal }}</p>
          <p class="bs-uitkomst__resultaat__eur">EUR</p>
        </div>
      </div>
      <form action="https://www.hypotheek.be/nl/uw-lening-aanvragen" method="get">
      <!-- <form action="https://auxifina.be/nl/uw-lening-aanvragen" method="get"> -->
        <input type="text" v-bind:value="teBerekenenIsLening === true ? berekendTotaalBedragCijfers : totaalBedrag" name="amount" class="hidden">
        <input type="text" v-bind:value="teBerekenenIsLening === true ? jaarFull : jaarMonth" name="duration" class="hidden">
        <input type="text" v-bind:value="teBerekenenIsLening === true ? jkpFull : jkpMonthCijfer" name="taeg" class="hidden" >
        <input type="text" v-bind:value="teBerekenenIsLening === true ? maandelijksBedrag : berekendBedragPerMaand" name="monthlyrepayment" class="hidden" >
        <input type="text" v-bind:value="teBerekenenIsLening === true ? totaalbedragInterest : totaalbedragInterestMaand" name="repayment" class="hidden" >
        <input type="text" value="lening-op-afbetaling" name="krediet-type" class="hidden" >
        <button class="bs-uitkomst__aanvragen" type="submit">Lening aanvragen</button>
      </form>
      <form class="form__extra" action="https://www.hypotheek.be/nl/contactformulier-consument/een-afspraak-maken" method="get">
      <!-- <form class="form__extra" action="https://auxifina.be/nl/contact" method="get"> -->
        <input type="text" v-bind:value="totaalBedrag" name="bedrag" class="hidden">
        <input type="text" v-bind:value="jaarMonth" name="periode" class="hidden">
        <input type="text" v-bind:value="true" name="simulator" class="hidden">
        <input type="text" v-bind:value="true" name="lalalaa" class="hidden">
        <input type="text" value="lening-op-afbetaling" name="krediet-type" class="hidden" >
        <button class="bs-uitkomst__aanvragen submit__formulier" type="submit">Of vraag een gepersonaliseerde simulatie</button>
      </form>
    </div>
    <div v-if="isBerekeningDone" class="bs-uitkomst__uitleg">
      <p>We maken gebruik van een rente van {{ rente }}% (<strong>5/5/5 formule en een quotiteit </= 70% </strong>), dit zorgt dat de jkp {{ rente }}% bedraagt.</p>
    </div>
    <a class="bs-uitkomst__jkp__info" v-on:click="toggleJkpInfo">Wat is de jkp?</a>
  </div>
</template>
<script>

export default {
  name: 'BerekeningResultaat',
  computed: {
    berekendTotaalBedrag () {
      let totaal = this.$store.state.berekendTotaalBedrag
      return totaal.toLocaleString('nl-BE', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },
    berekendTotaalBedragCijfers () {
      let totaal = this.$store.state.berekendTotaalBedrag
      return totaal
      // return totaal.toLocaleString('nl-BE', {
      //   minimumFractionDigits: 2,
      //   maximumFractionDigits: 2
      // });
    },
    berekendBedragPerMaand () {
      let maandelijks = this.$store.state.berekendBedragPerMaand
      // console.log(maandelijks);
      
      return maandelijks.toFixed(2);
      return maandelijks.toLocaleString('nl-BE', {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2
      });

    },
    berekendBedragPerMaandLocal () {
      let maandelijks = this.$store.state.berekendBedragPerMaand

      return maandelijks.toLocaleString('nl-BE', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },
    totaalBedrag () {
      return this.$store.state.totaalBedrag
    },
    jaarFull () {
      return this.$store.state.jaarFull
    },
    jaarMonth () {
      return this.$store.state.jaarMonth
    },
    teBerekenenIsLening () {
      return this.$store.state.teBerekenenIsLening
    },
    isBerekeningDone () {
      return this.$store.state.isBerekeningDone
    },
    jkpFull () {
      let jkp = this.$store.state.jkpFull;
      
      jkp = jkp * 100;
      jkp = jkp.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return jkp;
    },
    jkpMonth () {
      let jkp = this.$store.state.jkpMonth;
      
      jkp = jkp * 100;
      jkp = jkp.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return jkp;
    },
    jkpMonthCijfer () {
      let jkp = this.$store.state.jkpMonth;
      
      jkp = jkp * 100;
      // jkp = jkp.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return jkp.toFixed(2);
    },
    rente () {
      let rente = this.$store.state.rente;
      
      rente = rente * 100;
      rente = rente.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return rente;
    },
    maandelijksBedrag () {
      return this.$store.state.maandelijksBedrag;
    },
    test () {
      return this.$store.state.test;
    },
    totaalbedragInterest () {
      let interest = this.$store.state.interest;
      let totaalbedrag = this.$store.state.berekendTotaalBedrag


      let totaal = interest + totaalbedrag
      // totaal = totaal.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return totaal.toFixed(2);
    },
    totaalbedragInterestMaand () {
      let interest = this.$store.state.interestMaand
      let totaalbedrag = this.$store.state.totaalBedrag


      let totaal = interest + totaalbedrag
      // totaal = totaal.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return totaal.toFixed(2);
    }
  },
  methods: {
    toggleJkpInfo() {
      this.$store.commit('toggleJkpInfo', true)
    },
  }
}
</script>