<script>

export default {
  name: 'Legal',
  computed: {
    berekendTotaalBedrag () {
      let totaal = this.$store.state.berekendTotaalBedrag
      return totaal.toLocaleString('nl-BE', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },
    totaalBedrag () {
      let bedrag = this.$store.state.totaalBedrag

      bedrag = bedrag.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return bedrag;
    },
    notariskosten () {
      let bedrag = this.$store.state.notariskosten

      bedrag = bedrag.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return bedrag;
    },
    jaarFull () {
      return this.$store.state.jaarFull
    },
    jaarMonth () { 
      return this.$store.state.jaarMonth
    },
    teBerekenenIsLening () { 
      return this.$store.state.teBerekenenIsLening
    },
    jkpFull () { 
      let jkp = this.$store.state.jkpFull;
      
      jkp = jkp * 100;
      jkp = jkp.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return jkp;
    },
    jkpMonth () {
      let jkp = this.$store.state.jkpMonth;
      
      jkp = jkp * 100;
      jkp = jkp.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return jkp;
    },
    rente () {
      let rente = this.$store.state.rente;
      
      rente = rente * 100;
      rente = rente.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return rente;
    },
    interest () {
      let interest = this.$store.state.interest;
      interest = interest.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
     
      return interest;
    },
    interestMaand () {
      let interest = this.$store.state.interestMaand;
      interest = interest.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
     
      return interest;
    },
    totaalbedragInterest () {
      let interest = this.$store.state.interest;
      let totaalbedrag = this.$store.state.berekendTotaalBedrag

      let totaal = interest + totaalbedrag
      totaal = totaal.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return totaal;
    },
    totaalbedragInterestMaand () {
      let interest = this.$store.state.interestMaand
      let totaalbedrag = this.$store.state.totaalBedrag
      console.log("interest = " . interest)
      
      let totaal = interest + totaalbedrag
      totaal = totaal.toLocaleString('nl-BE', { maximumFractionDigits: 2 });
      return totaal;
    },
  },
  methods: {
    toggleJkpInfo() {
      this.$store.commit('toggleJkpInfo', false)  
    },
  },
}
</script>

