<template>
  <div class="">
    <!-- <h1>Bereken hier uw krediet:</h1> -->
    <form class="" action="">
      <div class="selectArea">
          <label class="loan__title" for="lening">Wat is het doel van jouw lening?</label>
          <select v-model='selected' class="selectMenu">
            <option value="gezinsuitgaven" selected>Gezinsuitgaven</option>
            <option value="auto">Auto</option>
            <option value="renovatie">Renovatie</option>
            <option value="vakantie">Vakantie</option>
            <option value="huwelijk">Huwelijk</option>
            <option value="hergroeperen">Hergroeperen kredieten</option>
          </select>
      </div>
      <div class="bs-form__question__div">
        <h2 class="loan__title">Voer het bedrag in dat u wilt lenen</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceTotalAmount();">-</button>
          <div>
            <label for="">EUR</label>
            <input v-model.number="bedrag" v-on:keydown.enter.prevent v-on:blur="check" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseTotalAmount();">+</button>
        </div>
      </div>
      <div class="bs-form__question__div">
        <h2 class="loan__title" for="">Voor een periode van</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceYears();">-</button>
          <div>
            <label for="">MAANDEN</label>
            <input v-model.number="jaar" v-on:keydown.enter.prevent v-on:blur="check" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseYears();">+</button>
        </div>
      </div>
      <div v-if="error" class="error_msg">Er is geen simulatie mogelijk voor dit kredietbedrag en aantal maanden. Probeer een ander kredietbedrag / periode.</div>
      <!-- <div v-if="error" class="error_msg">Aucune simulation n'est possible pour ce montant de crdit et ce nombre de mois. Essayez un montant / priode de crdit diffrent.</div> -->
      
      <button :disabled="error" v-bind:class="{buttonError : error}" v-if="mobile === true" v-scroll-to="'#element'" id="element" class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); showCalculation();">Berekenen</button>
      <button :disabled="error" v-bind:class="{buttonError : error}" v-else class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); showCalculation()">Berekenen</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'BerekeningSimulatieMaand',
  props: {
    
  },
  data() {
    return {
      mobile: false
    }
  },
  computed: {
    error(){
      return this.$store.state.error
    },
    berekendBedragPerMaand () {
      return this.$store.state.berekendBedragPerMaand
    },
    selected:{
      get () {
        return this.$store.state.typeLening
      },
      set (value) {
        this.$store.commit('updateLening', value)
        this.$store.commit('check')
      }
    },
    bedrag: {
      get () {
        return this.$store.state.totaalBedrag
      },
      set (value) {
        this.$store.commit('updateTotaalBedrag', value)
      }
    },
    jaar: {
      get () {
        return this.$store.state.jaarMonth
      },
      set (value) {
        this.$store.commit('updateYear', value)
      }
    }
  },
  mounted() {
    let vm = this;

    const changeButton = mediaQueryMobile => {
      if (mediaQueryMobile.matches) {
        vm.mobile = true
      } else {
        vm.mobile = false
      }
    }

    let mediaQueryMobile = window.matchMedia("(max-width: 700px)")
    changeButton(mediaQueryMobile)
    mediaQueryMobile.addListener(changeButton)
  },
  methods: {
    showCalculation(){
      this.$store.commit('berekeningIsDone')
    },
    check(){
      this.calculate()
    },
    reduceTotalAmount() {
      this.$store.commit('reduceTotalAmount')
      this.calculate() 
    },
    increaseTotalAmount() {
      this.$store.commit('increaseTotalAmount')
      this.calculate() 
    },
    reduceYears() {
      this.$store.commit('reduceYears')
      this.calculate() 
    },
    increaseYears() {
      this.$store.commit('increaseYears')
      this.calculate() 
    },
    calculate() {
      this.$store.commit('check')
      this.$store.commit('calculateRente')
      this.$store.commit('calculatePerMonth')
      // this.$store.commit('berekeningIsDone')
      this.$store.commit('calculateInterestMaand')
    },
    selectAllNumbers(e) {
      e.target.select()
      e.target.focus()
    },

  }
}
</script>