<template>
  <div class="">
    <!-- <h1>Bereken hier uw krediet:</h1> -->
    <form class="" action="">
      <div class="bs-form__question__div">
        <h2>Hoeveel wil je per maand afbetalen?</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceMonthlyAmount();">-</button>
          <div>
            <label for="">EUR</label>
            <input v-model.number="bedrag" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseMonthlyAmount();">+</button>
        </div>
      </div>
      <div class="bs-form__question__div">
        <h2 for="">Voor een periode van</h2>
        <div class="rij bs-form__input__div">
          <button v-on:click.prevent="reduceYears();">-</button>
          <div>
            <label for="">MAAND(EN)</label>
            <input v-model.number="jaar" v-on:click="selectAllNumbers" type="number">
          </div>
          <button v-on:click.prevent="increaseYears();">+</button>
        </div>
      </div>

      <button v-if="mobile === true" v-scroll-to="'#element'" id="element" class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); berekeningIsDone();  calculateNotarisK(); calculateJkp(); calculateInterest();">Berekenen</button>
      <button v-else class="bs-form__bereken" type="submit" v-on:click.prevent="calculate(); berekeningIsDone(); calculateNotarisK(); calculateJkp(); calculateInterest();">Berekenen</button>
    </form>

  </div>
</template>

<script>
export default  {
  name: 'BerekeningSimulatieMaand',
  props: {
    
  },
  data() {
    return {
      mobile: false
    }
  },
  computed: {
    berekendTotaalBedrag () {
      return this.$store.state.berekendTotaalBedrag
    },
    bedrag: {
      get () {
        return this.$store.state.maandelijksBedrag
      },
      set (value) {
        this.$store.commit('updateMaandelijksBedrag', value)
      }
    },
    jaar: {
      get () {
        return this.$store.state.jaarFull
      },
      set (value) { //blijkbaar nodig als de gebruiker de form input zelf verandert
        this.$store.commit('updateYear', value)
      }
    }
  },
  mounted() {
    let vm = this;

    const changeButton = mediaQueryMobile => {
      if (mediaQueryMobile.matches) {
        vm.mobile = true
      } else {
        vm.mobile = false
      }
    }

    let mediaQueryMobile = window.matchMedia("(max-width: 700px)")
    changeButton(mediaQueryMobile)
    mediaQueryMobile.addListener(changeButton)
  },
  methods: {
    reduceMonthlyAmount() {
      this.$store.commit('reduceMonthlyAmount')
    },
    increaseMonthlyAmount() {
      this.$store.commit('increaseMonthlyAmount')
    },
    reduceYears() {
      this.$store.commit('reduceYears')
      this.$store.commit('calculateTotaalBedrag')
      this.$store.commit('check_te_lenen_bedrag')
    },
    increaseYears() {
      this.$store.commit('increaseYears')
      this.$store.commit('calculateTotaalBedrag')
      this.$store.commit('check_te_lenen_bedrag')
    },
    calculate() {
      this.$store.commit('calculateTotaalBedrag')
    },
    berekeningIsDone() {
      this.$store.commit('berekeningIsDone')
    },
    calculateJkp() {
      this.$store.commit('calculateJkp')
    },
    calculateNotarisK() {
      this.$store.commit('calculateNotarisK')
    },
    selectAllNumbers(e) {
      e.target.select()
      e.target.focus()
    },
    calculateInterest() {
      this.$store.commit('calculateInterest')
    },
  }
}

</script>